@import "../../css/global.css";

.card-container {
  
  width: 25rem;
  display: inline-flex;
  margin: 5rem auto;
  align-items: stretch;
  justify-content: space-evenly;
  flex-wrap: wrap;
  flex-grow: 0;
  border-radius: 2rem 2rem 0 0;
  background: linear-gradient(145deg, #932e6b, #7b275a, rgba(15,15,15,.05));
  box-shadow: 22px 22px 44px #5f1e45, -22px -22px 44px #b33883;
 
}

.card-main {
  background-color: var(--black);
  border-radius: 2rem 2rem 0 0;
}

.card-body { 
  height:inherit;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;   
  align-content: stretch;
  justify-content: space-evenly;
  flex-wrap: wrap;
  flex-grow: 1;
  align-self: baseline; 
} 
.card-title {
  font-family: var(--ff-primary);
  font-size: 1.75rem;
  color: var(--white); 
}

.card-title:hover {
  text-decoration: none;
  color: var(--accent);
  transition: all 500ms ease;
  font-size: 1.85rem; 
}
.card-detail:hover {
  text-decoration: none;
  color: var(--accent);
  transition: all 500ms ease; 
}

.card-text {
  font-family: var(--ff-secondary);
  color: var(--white);
  font-size: 1rem;
  letter-spacing: 1px;
  font-weight: 400;
}

.card-footer {
  background-color: transparent !important;
}

.card-img {
  border-radius: 2rem 2rem 0 0;
  height: auto;
  -webkit-filter: grayscale(1);
  -webkit-transition: all 0.7s ease-in-out;
}

.card-img:hover {
  filter: none;
  -webkit-filter: grayscale(0);
  -webkit-transform: scale(1.01);
}

.card-detail {
 font-size: 1.2rem;
}

 